import React from 'react';
import { Button } from './ui/button';
import { Moon, Sun } from 'lucide-react';
import { useTheme } from '@/contexts/ThemeContext';
import { cn } from '@/lib/utils';

type ThemeToggleProps = {
  className?: string;
  variant?: 'default' | 'outline' | 'ghost';
};

export const ThemeToggle = React.memo<ThemeToggleProps>(({ 
  className, 
  variant = 'outline' 
}) => {
  const { themeMode, toggleTheme } = useTheme();
  
  return (
    <Button
      variant={variant}
      size="icon"
      onClick={toggleTheme}
      className={cn(
        "rounded-full transition-all hover:scale-110 focus-visible:scale-110 hover:bg-primary/10 hover:text-primary", 
        className
      )}
      aria-label={`Switch to ${themeMode === 'light' ? 'dark' : 'light'} mode`}
    >
      <div className="relative w-5 h-5 overflow-hidden">
        <Sun className={cn(
          "h-5 w-5 absolute top-0 left-0 transition-all duration-300",
          themeMode === 'light' 
            ? "opacity-0 rotate-90 scale-0" 
            : "opacity-100 rotate-0 scale-100"
        )} />
        <Moon className={cn(
          "h-5 w-5 absolute top-0 left-0 transition-all duration-300",
          themeMode === 'light' 
            ? "opacity-100 rotate-0 scale-100" 
            : "opacity-0 rotate-90 scale-0"
        )} />
      </div>
      <span className="sr-only">Toggle theme</span>
    </Button>
  );
});
