import React, { useState } from 'react';
import { cn } from '@/lib/utils';
import { useNavigation } from '@/hooks/useConsolidatedApp';
import { ViewType } from '@/store/appStore';
import { useWorkoutTemplates } from '@/hooks/useConsolidatedApp';
import { 
  Home, Dumbbell, BarChart3, Settings, PlayCircle, MoreHorizontal, X, CheckSquare
} from 'lucide-react';
import { Button } from '@/components/ui/button';

interface BottomNavItemProps {
  icon: React.ReactNode;
  label: string;
  view?: ViewType;
  isActive?: boolean;
  onClick: () => void;
  isMore?: boolean;
}

const BottomNavItem: React.FC<BottomNavItemProps> = ({ 
  icon, 
  label, 
  isActive,
  onClick,
  isMore = false
}) => {
  return (
    <button 
      className={cn(
        "flex flex-col items-center justify-center flex-1 px-1 py-1.5 sm:px-2 focus:outline-none transition-all duration-200 ease-in-out transform active:scale-95",
        isActive && !isMore
          ? "text-primary" 
          : "text-muted-foreground hover:text-foreground",
        isMore && "relative"
      )}
      onClick={onClick}
      aria-label={label}
    >
      <div className={cn(
        "p-1.5 rounded-full transition-all duration-200 ease-in-out",
        isActive && !isMore ? "bg-primary/10 scale-110" : "hover:bg-muted/50"
      )}>
        {icon}
      </div>
      <span className={cn(
        "text-[0.6rem] sm:text-xs mt-0.5 font-medium transition-all duration-200 ease-in-out truncate max-w-[4em] sm:max-w-none",
        isActive && !isMore ? "scale-105 text-primary" : "text-muted-foreground"
      )}>{label}</span>
    </button>
  );
};

// More Menu Overlay Component
const MoreMenuOverlay: React.FC<{
  isOpen: boolean;
  onClose: () => void;
  onItemClick: (view: ViewType) => void;
  currentView: ViewType;
}> = ({ isOpen, onClose, onItemClick, currentView }) => {
  if (!isOpen) return null;

  // Define sections for the more menu
  const moreMenuSections = [
    {
      title: "Fitness & Health",
      items: [
        { view: 'templates' as ViewType, label: 'Templates', icon: '📋' },
        { view: 'muscleFatigue' as ViewType, label: 'Muscle Fatigue', icon: '💪' },
        { view: 'schedule' as ViewType, label: 'Schedule', icon: '📅' },        { view: 'nutrition' as ViewType, label: 'Nutrition', icon: '🍎' },
        { view: 'health-tracking' as ViewType, label: 'Health', icon: '❤️' },
        { view: 'sleep-tracking' as ViewType, label: 'Sleep', icon: '🛏️' },
      ]
    },
    {
      title: "Finance",
      items: [
        { view: 'budget-tracker' as ViewType, label: 'Budget', icon: '💰', comingSoon: true },
        { view: 'investment-tracker' as ViewType, label: 'Investments', icon: '📈', comingSoon: true },
        { view: 'expense-management' as ViewType, label: 'Expenses', icon: '💳', comingSoon: true },
      ]
    },
    {
      title: "Career & Productivity",
      items: [
        { view: 'career-goals' as ViewType, label: 'Career', icon: '💼', comingSoon: true },
        { view: 'skill-development' as ViewType, label: 'Skills', icon: '🎓', comingSoon: true },
        { view: 'goal-setting' as ViewType, label: 'Goals', icon: '🎯', comingSoon: true },
        { view: 'habit-tracker' as ViewType, label: 'Habits', icon: '✅', comingSoon: true },
      ]
    },
    {
      title: "Personal",
      items: [
        { view: 'journal' as ViewType, label: 'Journal', icon: '📝', comingSoon: true },
        { view: 'mood-tracker' as ViewType, label: 'Mood', icon: '😊', comingSoon: true },
        { view: 'relationship-tracker' as ViewType, label: 'Relations', icon: '❤️', comingSoon: true },
      ]
    }
  ];
  
  return (
    <div className="fixed inset-0 bg-black/50 z-50 md:hidden" onClick={onClose}>
      <div 
        className="fixed bottom-[var(--bottom-nav-height,60px)] left-0 right-0 bg-background border-t border-border rounded-t-xl p-4 max-h-[60vh] overflow-y-auto"
        onClick={(e) => e.stopPropagation()}
      >
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold">More Features</h3>
          <Button variant="ghost" size="icon" onClick={onClose}>
            <X className="h-5 w-5" />
          </Button>
        </div>
        
        {moreMenuSections.map(section => (
          <div key={section.title} className="mb-6">
            <h4 className="text-sm font-medium text-muted-foreground mb-3">{section.title}</h4>
            <div className="grid grid-cols-3 gap-3">
              {section.items.map(item => {
                const isActive = currentView === item.view;
                const isComingSoon = item.comingSoon;
                
                return (
                  <Button
                    key={item.view}
                    variant="ghost"
                    className={cn(
                      "flex flex-col items-center p-3 h-auto space-y-2 relative",
                      isActive ? "bg-primary/15 text-primary" : "",
                      isComingSoon ? "opacity-60" : ""
                    )}
                    onClick={() => {
                      if (!isComingSoon) {
                        onItemClick(item.view);
                        onClose();
                      }
                    }}
                    disabled={isComingSoon}
                  >
                    <span className="text-lg">{item.icon}</span>
                    <span className="text-xs text-center leading-tight">{item.label}</span>
                    {isComingSoon && (
                      <span className="absolute -top-1 -right-1 text-[0.5rem] bg-muted text-muted-foreground px-1 py-0.5 rounded-full">
                        Soon
                      </span>
                    )}
                  </Button>
                );
              })}
            </div>
          </div>
        ))}
        
        <div className="pt-4 border-t border-border">
          <p className="text-xs text-muted-foreground text-center">
            More features coming soon to help manage all aspects of your life! 🚀
          </p>
        </div>
      </div>
    </div>
  );
};

export const BottomNav: React.FC = () => {
  const { currentView, setCurrentView } = useNavigation();
  const { activeWorkout } = useWorkoutTemplates();
  const [showMoreMenu, setShowMoreMenu] = useState(false);
  
  if (typeof window === 'undefined') return null;
  
  // Core navigation items for bottom nav (most important/frequently used)
  const coreNavItems = [
    { view: 'dashboard' as ViewType, label: 'Home', icon: <Home className="h-5 w-5 sm:h-6 sm:w-6" /> },
    // Conditionally include the active workout button if there's an active workout
    ...(activeWorkout ? [{ 
      view: 'workout' as ViewType, 
      label: 'Workout', 
      icon: <PlayCircle className="h-5 w-5 sm:h-6 sm:w-6 text-primary" /> 
    }] : []),
    { view: 'workouts' as ViewType, label: 'Workouts', icon: <Dumbbell className="h-5 w-5 sm:h-6 sm:w-6" /> },
    { view: 'todos' as ViewType, label: 'Tasks', icon: <CheckSquare className="h-5 w-5 sm:h-6 sm:w-6" /> },
    { view: 'stats' as ViewType, label: 'Stats', icon: <BarChart3 className="h-5 w-5 sm:h-6 sm:w-6" /> },
    { view: 'settings' as ViewType, label: 'Settings', icon: <Settings className="h-5 w-5 sm:h-6 sm:w-6" /> },
  ];
  
  return (
    <>
      <div className="md:hidden fixed bottom-0 left-0 right-0 bg-background/80 backdrop-blur-lg border-t border-border/60 z-40 flex justify-around items-center h-[var(--bottom-nav-height,60px)] shadow-top transition-transform duration-300 ease-in-out"
           style={{ contain: 'layout style paint' }} 
      >
        {coreNavItems.map((item) => (
          <BottomNavItem
            key={item.view}
            icon={item.icon}
            label={item.label}
            view={item.view}
            isActive={currentView === item.view}
            onClick={() => setCurrentView(item.view)}
          />
        ))}
        
        {/* More menu button */}
        <BottomNavItem
          icon={<MoreHorizontal className="h-5 w-5 sm:h-6 sm:w-6" />}
          label="More"
          isActive={showMoreMenu}
          onClick={() => setShowMoreMenu(true)}
          isMore={true}
        />
      </div>
      
      {/* More menu overlay */}
      <MoreMenuOverlay
        isOpen={showMoreMenu}
        onClose={() => setShowMoreMenu(false)}
        onItemClick={setCurrentView}
        currentView={currentView}
      />
    </>
  );
};
