import { WeightUnit, HeightUnit, TemperatureUnit } from '@/store/appStore';

// Weight conversion constants
const KG_TO_LBS = 2.20462;
const LBS_TO_KG = 0.453592;

// Height conversion constants
const CM_TO_INCH = 0.393701;
const INCH_TO_CM = 2.54;
const CM_TO_FEET = 0.0328084;
const FEET_TO_CM = 30.48;

// Temperature conversion functions
const celsiusToFahrenheit = (celsius: number): number => (celsius * 9/5) + 32;
const fahrenheitToCelsius = (fahrenheit: number): number => (fahrenheit - 32) * 5/9;

// Weight conversion functions
export const convertWeight = (value: number, fromUnit: WeightUnit, toUnit: WeightUnit): number => {
  if (fromUnit === toUnit) return value;
  
  if (fromUnit === 'kg' && toUnit === 'lbs') {
    return value * KG_TO_LBS;
  } else if (fromUnit === 'lbs' && toUnit === 'kg') {
    return value * LBS_TO_KG;
  }
  
  return value;
};

// Height conversion functions
export const convertHeight = (value: number, fromUnit: HeightUnit, toUnit: HeightUnit): number => {
  if (fromUnit === toUnit) return value;
  
  if (fromUnit === 'cm' && toUnit === 'inch') {
    return value * CM_TO_INCH;
  } else if (fromUnit === 'inch' && toUnit === 'cm') {
    return value * INCH_TO_CM;
  }
  
  return value;
};

// Temperature conversion functions
export const convertTemperature = (value: number, fromUnit: TemperatureUnit, toUnit: TemperatureUnit): number => {
  if (fromUnit === toUnit) return value;
  
  if (fromUnit === 'celsius' && toUnit === 'fahrenheit') {
    return celsiusToFahrenheit(value);
  } else if (fromUnit === 'fahrenheit' && toUnit === 'celsius') {
    return fahrenheitToCelsius(value);
  }
  
  return value;
};

// Format weight with unit
export const formatWeight = (value: number, unit: WeightUnit): string => {
  return `${value.toFixed(1)} ${unit}`;
};

// Format height with unit
export const formatHeight = (value: number, unit: HeightUnit): string => {
  return `${value.toFixed(1)} ${unit}`;
};

// Format temperature with unit
export const formatTemperature = (value: number, unit: TemperatureUnit): string => {
  return `${value.toFixed(1)}°${unit === 'celsius' ? 'C' : 'F'}`;
};

// Convert between metric systems (metric, imperial, indian)
export const convertMetricSystem = {
  weight: {
    metric: { unit: 'kg' as WeightUnit },
    imperial: { unit: 'lbs' as WeightUnit },
    indian: { unit: 'kg' as WeightUnit }
  },
  height: {
    metric: { unit: 'cm' as HeightUnit },
    imperial: { unit: 'inch' as HeightUnit },
    indian: { unit: 'cm' as HeightUnit }
  },
  temperature: {
    metric: { unit: 'celsius' as TemperatureUnit },
    imperial: { unit: 'fahrenheit' as TemperatureUnit },
    indian: { unit: 'celsius' as TemperatureUnit }
  }
}; 