import { useAppStore } from '@/store/appStore';
import { 
  convertWeight, 
  convertHeight, 
  convertTemperature,
  formatWeight,
  formatHeight,
  formatTemperature
} from '@/utils/unitConversion';

export const useMetricConversion = () => {
  const { metricSettings } = useAppStore();

  const convertAndFormatWeight = (value: number, fromUnit?: 'kg' | 'lbs') => {
    const converted = convertWeight(value, fromUnit || metricSettings.weightUnit, metricSettings.weightUnit);
    return formatWeight(converted, metricSettings.weightUnit);
  };

  const convertAndFormatHeight = (value: number, fromUnit?: 'cm' | 'inch') => {
    const converted = convertHeight(value, fromUnit || metricSettings.heightUnit, metricSettings.heightUnit);
    return formatHeight(converted, metricSettings.heightUnit);
  };

  const convertAndFormatTemperature = (value: number, fromUnit?: 'celsius' | 'fahrenheit') => {
    const converted = convertTemperature(value, fromUnit || metricSettings.temperatureUnit, metricSettings.temperatureUnit);
    return formatTemperature(converted, metricSettings.temperatureUnit);
  };

  const getWeightUnit = () => metricSettings.weightUnit;
  const getHeightUnit = () => metricSettings.heightUnit;
  const getTemperatureUnit = () => metricSettings.temperatureUnit;

  return {
    convertAndFormatWeight,
    convertAndFormatHeight,
    convertAndFormatTemperature,
    getWeightUnit,
    getHeightUnit,
    getTemperatureUnit,
    metricSettings
  };
}; 