import { useNavigation, useAuth } from '@/hooks/useConsolidatedApp';
import { ViewType } from '@/store/appStore';
import { useWorkoutTemplates } from '@/hooks/useConsolidatedApp';

interface SidebarNavProps extends React.HTMLAttributes<HTMLElement> {
  isCollapsed: boolean;
  toggleSidebar: () => void;
}

interface NavItem {
  view: ViewType;
  label: string;
  icon: React.ElementType;
  comingSoon?: boolean;
}

interface NavSection {
  title: string;
  items: NavItem[];
  collapsible?: boolean;
  defaultExpanded?: boolean;
}

const coreNavItems: NavItem[] = [
  { view: 'dashboard' as ViewType, label: 'Dashboard', icon: Home },
  { view: 'profile' as ViewType, label: 'Profile', icon: User },
  { view: 'settings' as ViewType, label: 'Settings', icon: Settings },
];

const navSections: NavSection[] = [
  {
    title: 'Fitness & Health',
    defaultExpanded: true,
    items: [
      { view: 'workouts' as ViewType, label: 'Workouts', icon: Dumbbell },
      { view: 'templates' as ViewType, label: 'Templates', icon: BookTemplate },
      { view: 'muscleFatigue' as ViewType, label: 'Muscle Fatigue', icon: Activity },
      { view: 'schedule' as ViewType, label: 'Schedule', icon: CalendarClock },
      { view: 'stats' as ViewType, label: 'Statistics', icon: BarChart3 },      { view: 'nutrition' as ViewType, label: 'Nutrition', icon: Apple },
      { view: 'meal-planning' as ViewType, label: 'Meal Planning', icon: CheckSquare },
      { view: 'health-tracking' as ViewType, label: 'Health Tracking', icon: Heart },
      { view: 'sleep-tracking' as ViewType, label: 'Sleep Tracking', icon: Bed },
      { view: 'meditation' as ViewType, label: 'Meditation', icon: Brain },
    ]
  },
  {
    title: 'Finance',
    collapsible: true,
    items: [
      { view: 'budget-tracker' as ViewType, label: 'Budget Tracker', icon: Calculator, comingSoon: true },
      { view: 'investment-tracker' as ViewType, label: 'Investments', icon: TrendingUp, comingSoon: true },
      { view: 'expense-management' as ViewType, label: 'Expenses', icon: CreditCard, comingSoon: true },
      { view: 'financial-goals' as ViewType, label: 'Financial Goals', icon: Target, comingSoon: true },
      { view: 'debt-tracker' as ViewType, label: 'Debt Tracker', icon: DollarSign, comingSoon: true },
    ]
  },
  {
    title: 'Career & Education',
    collapsible: true,
    items: [
      { view: 'skill-development' as ViewType, label: 'Skill Development', icon: GraduationCap, comingSoon: true },
      { view: 'career-goals' as ViewType, label: 'Career Goals', icon: Briefcase, comingSoon: true },
      { view: 'networking' as ViewType, label: 'Networking', icon: Users, comingSoon: true },
      { view: 'certifications' as ViewType, label: 'Certifications', icon: Trophy, comingSoon: true },
      { view: 'job-search' as ViewType, label: 'Job Search', icon: MapPin, comingSoon: true },
    ]
  },
  {
    title: 'Relationships & Social',
    collapsible: true,
    items: [
      { view: 'relationship-tracker' as ViewType, label: 'Relationships', icon: Heart, comingSoon: true },
      { view: 'social-calendar' as ViewType, label: 'Social Calendar', icon: CalendarClock, comingSoon: true },
      { view: 'gift-tracker' as ViewType, label: 'Gift Tracker', icon: Gift, comingSoon: true },
      { view: 'contact-management' as ViewType, label: 'Contacts', icon: Phone, comingSoon: true },
    ]
  },
  {
    title: 'General / Productivity',
    collapsible: true,
    items: [
      { view: 'todos' as ViewType, label: 'Tasks', icon: CheckSquare },
      { view: 'habit-tracker' as ViewType, label: 'Habit Tracker', icon: CheckSquare, comingSoon: true },
      { view: 'time-management' as ViewType, label: 'Time Management', icon: Clock, comingSoon: true },
      { view: 'goal-setting' as ViewType, label: 'Goal Setting', icon: Target, comingSoon: true },
      { view: 'project-management' as ViewType, label: 'Projects', icon: FileText, comingSoon: true },
      { view: 'book-tracker' as ViewType, label: 'Reading List', icon: BookOpen, comingSoon: true },
    ]
  },
  {
    title: 'Personal Development',
    collapsible: true,
    items: [
      { view: 'journal' as ViewType, label: 'Journal', icon: PenTool, comingSoon: true },
      { view: 'mood-tracker' as ViewType, label: 'Mood Tracker', icon: Smile, comingSoon: true },
      { view: 'gratitude-log' as ViewType, label: 'Gratitude Log', icon: Sun, comingSoon: true },
      { view: 'values-tracker' as ViewType, label: 'Values Tracker', icon: Lightbulb, comingSoon: true },
      { view: 'life-vision' as ViewType, label: 'Life Vision', icon: Trophy, comingSoon: true },
    ]
  }
];

export function SidebarNav({ className, isCollapsed, toggleSidebar }: SidebarNavProps) {
  const { setCurrentView, currentView } = useNavigation();
  const { logout } = useAuth();
  const { activeWorkout } = useWorkoutTemplates();
  const [expandedSections, setExpandedSections] = useState<Record<string, boolean>>(() => {
    // Initialize with default expanded states
    const initial: Record<string, boolean> = {};
    navSections.forEach(section => {
      initial[section.title] = section.defaultExpanded || false;
    });
    return initial;
  });

  const toggleSection = (sectionTitle: string) => {
    setExpandedSections(prev => ({
      ...prev,
      [sectionTitle]: !prev[sectionTitle]
    }));
  };

  const NavButton = ({ item, isActiveWorkout = false }: { item: NavItem; isActiveWorkout?: boolean }) => (
    <Button
      key={item.label}
      variant="ghost"
      onClick={() => setCurrentView(item.view)}
      disabled={item.comingSoon}
      className={cn(
        "flex items-center gap-3 rounded-lg px-3 py-2.5 text-sm transition-all duration-base w-full justify-start relative",
        "hover:bg-primary/10 hover:text-primary",
        currentView === item.view 
          ? "bg-primary/15 text-primary font-medium shadow-sm" 
          : "text-muted-foreground",
        isCollapsed && "justify-center h-11 w-11 p-0",
        item.comingSoon && "opacity-60 cursor-not-allowed",
        isActiveWorkout && "bg-primary/20 text-primary border border-primary/30"
      )}
      title={isCollapsed ? item.label : undefined}
    >
      <item.icon className={cn("h-5 w-5 shrink-0", isCollapsed ? "m-0" : "")} />
      {!isCollapsed && (
        <>
          <span className="transition-opacity duration-base whitespace-nowrap overflow-hidden text-ellipsis flex-1 text-left">
            {item.label}
          </span>
          {item.comingSoon && (
            <span className="text-xs bg-muted px-1.5 py-0.5 rounded-full text-muted-foreground">Soon</span>
          )}
          {isActiveWorkout && (
            <span className="text-xs bg-primary/20 px-1.5 py-0.5 rounded-full text-primary">Active</span>
          )}
        </>
      )}
      {isCollapsed && <span className="sr-only">{item.label}</span>}
    </Button>
  );

  const SectionHeader = ({ section }: { section: NavSection }) => {
    if (isCollapsed) return null;
    
    return (
      <div className="flex items-center justify-between px-3 py-2 mt-4 first:mt-0">
        <h3 className="text-xs font-semibold text-muted-foreground uppercase tracking-wider">
          {section.title}
        </h3>
        {section.collapsible && (
          <Button
            variant="ghost"
            size="sm"
            onClick={() => toggleSection(section.title)}
            className="h-6 w-6 p-0 hover:bg-muted/50"
          >
            {expandedSections[section.title] ? (
              <ChevronUp className="h-3 w-3" />
            ) : (
              <ChevronDown className="h-3 w-3" />
            )}
          </Button>
        )}
      </div>
    );
  };

  return (
    <div className={cn(
      "relative h-full bg-background text-card-foreground border-r border-r-border/40 transition-all duration-base backdrop-blur-md flex flex-col",
      isCollapsed ? "w-[var(--sidebar-collapsed-width,80px)]" : "w-[var(--sidebar-width,280px)]",
      className
    )}>
      <div className={cn(
        "flex h-[var(--header-height,64px)] items-center border-b border-b-border/40 px-3 sm:px-4 sticky top-0 z-20 bg-background/70 backdrop-blur-md",
        isCollapsed ? "justify-center" : "justify-between"
      )}>
        {!isCollapsed && (
          <h1 className="text-lg font-semibold tracking-tight overflow-hidden text-ellipsis whitespace-nowrap bg-gradient-to-r from-primary to-accent bg-clip-text text-transparent">
            Life Manager
          </h1>
        )}
        <Button 
          variant="ghost" 
          size="icon" 
          onClick={toggleSidebar}
          className="rounded-full hover:bg-primary/10 hover:text-primary transition-button duration-base"
        >
          {isCollapsed ? (
            <ChevronRight className="h-4 w-4" />
          ) : (
            <ChevronLeft className="h-4 w-4" />
          )}
          <span className="sr-only">{isCollapsed ? "Expand sidebar" : "Collapse sidebar"}</span>
        </Button>
      </div>

      <nav className="flex-1 overflow-y-auto py-4 sm:py-6 px-2 sm:px-4 space-y-1">
        {/* Core Navigation */}
        {coreNavItems.map((item) => (
          <NavButton key={item.label} item={item} />
        ))}

        {/* Active Workout Button (if exists) */}
        {activeWorkout && (
          <div className="py-2">
            {!isCollapsed && (
              <div className="px-3 py-2">
                <h3 className="text-xs font-semibold text-muted-foreground uppercase tracking-wider">
                  Current Session
                </h3>
              </div>
            )}
            <NavButton 
              item={{ view: 'workout' as ViewType, label: 'Active Workout', icon: PlayCircle }}
              isActiveWorkout={true}
            />
          </div>
        )}

        {/* Life Management Sections */}
        {navSections.map((section) => {
          const isExpanded = !section.collapsible || expandedSections[section.title];
          
          return (
            <div key={section.title}>
              <SectionHeader section={section} />
              {(isExpanded || isCollapsed) && (
                <div className={cn("space-y-1", !isCollapsed && "ml-2")}>
                  {section.items.map((item) => (
                    <NavButton key={item.label} item={item} />
                  ))}
                </div>
              )}
            </div>
          );
        })}
      </nav>

      <div className={cn(
        "border-t border-t-border/40 p-3 sm:p-4 mt-auto sticky bottom-0 bg-background/70 backdrop-blur-md",
        isCollapsed ? "px-2" : "px-4"
      )}>
        <Button 
          variant={isCollapsed ? "ghost" : "outline"}
          size={isCollapsed ? "icon" : "default"}
          className={cn(
            "transition-all duration-base w-full",
            isCollapsed 
              ? "h-10 w-10 p-0 rounded-full hover:bg-destructive/10 hover:text-destructive border-destructive/20" 
              : "justify-start gap-2 shadow-sm hover:shadow-md rounded-lg hover:bg-destructive/10 hover:text-destructive border-destructive/30 text-destructive-foreground"
          )}
          onClick={logout}
          title={isCollapsed ? "Sign Out" : undefined}
        >
          <LogOut className={cn("h-4 w-4 shrink-0", isCollapsed ? "" : "mr-2")} />
          {!isCollapsed && (
            <span className="whitespace-nowrap overflow-hidden text-ellipsis">Sign Out</span>
          )}
          {isCollapsed && <span className="sr-only">Sign Out</span>}
        </Button>
      </div>
    </div>
  );
}
