import React, { useEffect, useRef, useState } from 'react';
import { 
  Dialog, 
  DialogContent, 
  DialogHeader, 
  DialogTitle, 
  DialogFooter 
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Slider } from '@/components/ui/slider';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { useWorkoutLogs, useExercises } from '@/hooks/useConsolidatedApp';
import { cn } from '@/lib/utils';
import { WorkoutLogEntry } from '../../types/Workout_Log';
import { useMetricConversion } from '@/hooks/useMetricConversion';

// Form schema that matches WorkoutLogEntry type (minus id field)
const formSchema = z.object({
  date: z.string().min(1, "Date is required"),
  muscleGroup: z.string().min(1, "Muscle group is required"),
  exercise: z.string().min(1, "Exercise is required"),
  reps: z.coerce.number().min(1, "Reps must be at least 1"),
  weight: z.coerce.number().min(0, "Weight must be non-negative"),
  rating: z.coerce.number().min(1, "Rating must be between 1-10").max(10, "Rating must be between 1-10"),
  restTime: z.coerce.number().min(0, "Rest time must be non-negative").nullable(),
  rowIndex: z.number().optional(),
  isSynced: z.boolean().optional().default(false),
  pendingAction: z.enum(['add', 'update', 'delete']).nullable().optional().default('add')
});

// Define form types that match WorkoutLogEntry
type FormSchema = z.infer<typeof formSchema>;
type FormData = FormSchema;

interface AddWorkoutLogModalProps {
  isOpen: boolean;
  onClose: () => void;
  editEntry?: WorkoutLogEntry | Partial<WorkoutLogEntry> | null;
}

// Autocomplete suggestion component
interface AutocompleteInputProps {
  value: string;
  onChange: (value: string) => void;
  suggestions: string[];
  placeholder: string;
  className?: string;
  key?: string | number; // Add key prop to force reset
  reset?: boolean; // Add reset prop to force clear state
}

const AutocompleteInput: React.FC<AutocompleteInputProps> = ({
  value,
  onChange,
  suggestions,
  placeholder,
  className
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [filteredSuggestions, setFilteredSuggestions] = useState<string[]>([]);
  const [highlightedIndex, setHighlightedIndex] = useState(-1);
  const inputRef = useRef<HTMLInputElement>(null);
  const listRef = useRef<HTMLUListElement>(null);
  const timeoutRef = useRef<NodeJS.Timeout | null>(null);
  const componentMountedRef = useRef(true);

  // Force close dropdown and reset all state
  const resetState = () => {
    if (!componentMountedRef.current) return;
    setIsOpen(false);
    setFilteredSuggestions([]);
    setHighlightedIndex(-1);
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
      timeoutRef.current = null;
    }
  };

  // Reset component state on mount
  useEffect(() => {
    componentMountedRef.current = true;
    resetState();
    return () => {
      componentMountedRef.current = false;
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
      }
    };
  }, []);
  // Filter suggestions and manage dropdown visibility - only if component is mounted
  useEffect(() => {
    if (!componentMountedRef.current) return;
    
    try {
      if (value && value.trim() && suggestions.length > 0) {
        const filtered = suggestions.filter(suggestion =>
          suggestion.toLowerCase().includes(value.toLowerCase())
        );
        setFilteredSuggestions(filtered);
        // Don't auto-open dropdown on mount - only on user interaction
        if (document.activeElement === inputRef.current) {
          setIsOpen(filtered.length > 0);
        }
      } else {
        setFilteredSuggestions([]);
        setIsOpen(false);
      }
      setHighlightedIndex(-1);
    } catch (error) {
      console.error('Error filtering suggestions:', error);
      setFilteredSuggestions([]);
      setIsOpen(false);
      setHighlightedIndex(-1);
    }
  }, [value, suggestions]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    onChange(e.target.value);
  };

  const handleSuggestionClick = (suggestion: string) => {
    onChange(suggestion);
    resetState();
    inputRef.current?.blur();
  };
  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (!isOpen || !componentMountedRef.current) return;

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault();
        setHighlightedIndex(prev => 
          prev < filteredSuggestions.length - 1 ? prev + 1 : prev
        );
        break;
      case 'ArrowUp':
        e.preventDefault();
        setHighlightedIndex(prev => prev > 0 ? prev - 1 : -1);
        break;
      case 'Enter':
        e.preventDefault();
        if (highlightedIndex >= 0 && filteredSuggestions[highlightedIndex]) {
          handleSuggestionClick(filteredSuggestions[highlightedIndex]);
        } else {
          resetState();
        }
        break;
      case 'Escape':
        e.preventDefault();
        resetState();
        inputRef.current?.blur();
        break;
      case 'Tab':
        // Allow tab to work normally but close dropdown
        resetState();
        break;
    }
  };
  const handleBlur = () => {
    // Use a longer delay to ensure suggestion clicks work
    timeoutRef.current = setTimeout(() => {
      if (componentMountedRef.current) {
        resetState();
      }
    }, 300); // Increased timeout for better UX
  };
  
  const handleFocus = () => {
    // Clear any pending timeout when focusing
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
      timeoutRef.current = null;
    }
    // Only show dropdown if there's a value, filtered suggestions, and user is actively focusing
    if (value && value.trim() && componentMountedRef.current) {
      const filtered = suggestions.filter(suggestion =>
        suggestion.toLowerCase().includes(value.toLowerCase())
      );
      if (filtered.length > 0) {
        setFilteredSuggestions(filtered);
        setIsOpen(true);
      }
    }
  };

  return (
    <div className="relative">
      <Input
        ref={inputRef}
        value={value}
        onChange={handleInputChange}
        onKeyDown={handleKeyDown}
        onBlur={handleBlur}
        onFocus={handleFocus}
        placeholder={placeholder}
        className={className}
      />
      {isOpen && filteredSuggestions.length > 0 && (
        <ul
          ref={listRef}
          className="absolute z-50 w-full mt-1 bg-background border border-border rounded-md shadow-lg max-h-60 overflow-auto"
          onMouseDown={(e) => {
            // Prevent blur event from firing when clicking on suggestions
            e.preventDefault();
          }}
        >
          {filteredSuggestions.map((suggestion, index) => (
            <li
              key={suggestion}
              onMouseDown={(e) => {
                // Prevent default to avoid blur
                e.preventDefault();
                handleSuggestionClick(suggestion);
              }}
              className={cn(
                "px-3 py-2 cursor-pointer text-foreground hover:bg-muted transition-colors",
                highlightedIndex === index && "bg-muted"
              )}
            >
              {suggestion}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

const AddWorkoutLogModal: React.FC<AddWorkoutLogModalProps> = ({ 
  isOpen, 
  onClose, 
  editEntry = null 
}) => {
  // Add a key state to force remounting AutocompleteInput components
  const [modalKey, setModalKey] = useState(0);
  const [isSubmitting, setIsSubmitting] = useState(false);
  
  const { addWorkoutLog, editWorkoutLog } = useWorkoutLogs();
  const { 
    exerciseGroups, 
    fetchExercises, 
    getExercisesByMuscleGroup, 
    getMuscleGroupsByExercise,
    getAllUniqueExerciseNames
  } = useExercises();
  const { convertAndFormatWeight, getWeightUnit } = useMetricConversion();

  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: {
      date: new Date().toISOString().split('T')[0],
      muscleGroup: '',
      exercise: '',
      reps: 1,
      weight: 0,
      rating: 5,
      restTime: null,
      rowIndex: undefined,
      isSynced: false,
      pendingAction: 'add' as const
    },
  });  // Handle modal close with cleanup
  const handleClose = () => {
    // Force form reset and autocomplete cleanup
    const defaultValues = {
      date: new Date().toISOString().split('T')[0],
      muscleGroup: '',
      exercise: '',
      reps: 1,
      weight: 0,
      rating: 5,
      restTime: null,
      rowIndex: undefined,
      isSynced: false,
      pendingAction: 'add' as const
    };
    
    form.reset(defaultValues);
    // Force autocomplete components to unmount and remount
    setModalKey(prev => prev + 1);
    onClose();
  };

  // Fetch exercises data when modal opens
  useEffect(() => {
    if (isOpen && exerciseGroups.length === 0) {
      fetchExercises();
    }
  }, [isOpen, exerciseGroups.length, fetchExercises]);  // Reset form and increment key when modal opens/closes
  useEffect(() => {
    if (isOpen) {
      // When opening, increment key to force autocomplete reset
      setModalKey(prev => prev + 1);
    } else {
      // Reset form when modal closes with a small delay to avoid conflicts
      const timeoutId = setTimeout(() => {
        if (!isOpen) { // Double-check modal is still closed
          const defaultValues = {
            date: new Date().toISOString().split('T')[0],
            muscleGroup: '',
            exercise: '',
            reps: 1,
            weight: 0,
            rating: 5,
            restTime: null,
            rowIndex: undefined,
            isSynced: false,
            pendingAction: 'add' as const
          };
          form.reset(defaultValues);
          setModalKey(prev => prev + 1); // Force autocomplete remount
        }
      }, 100);
      
      return () => clearTimeout(timeoutId);
    }
  }, [isOpen, form]);
  // Handle edit mode
  useEffect(() => {
    if (editEntry && isOpen) {
      // Only populate form if modal is open and editEntry exists
      const editValues = {
        date: editEntry.date || new Date().toISOString().split('T')[0],
        muscleGroup: editEntry.muscleGroup || '',
        exercise: editEntry.exercise || '',
        reps: editEntry.reps || 1,
        weight: editEntry.weight || 0,
        rating: editEntry.rating || 5,
        restTime: editEntry.restTime || null,
        rowIndex: editEntry.rowIndex,
        isSynced: editEntry.isSynced || false,
        pendingAction: ('id' in editEntry && editEntry.id) ? 'update' as const : 'add' as const
      };
      form.reset(editValues);
    } else if (isOpen && !editEntry) {
      // Reset to default values when opening without editEntry
      const defaultValues = {
        date: new Date().toISOString().split('T')[0],
        muscleGroup: '',
        exercise: '',
        reps: 1,
        weight: 0,
        rating: 5,
        restTime: null,
        rowIndex: undefined,
        isSynced: false,
        pendingAction: 'add' as const
      };
      form.reset(defaultValues);
    }
  }, [editEntry, isOpen, form]);
  // Get dynamic options based on current selections
  const currentMuscleGroup = form.watch('muscleGroup');
  const currentExercise = form.watch('exercise');

  // Get muscle groups from API data with error handling
  const availableMuscleGroups = React.useMemo(() => {
    try {
      return exerciseGroups.map(group => group.muscleGroup).filter(Boolean);
    } catch (error) {
      console.error('Error getting muscle groups:', error);
      return [];
    }
  }, [exerciseGroups]);

  // Get exercises based on current muscle group selection or all exercises
  const availableExercises = React.useMemo(() => {
    try {
      return currentMuscleGroup 
        ? getExercisesByMuscleGroup(currentMuscleGroup)
        : getAllUniqueExerciseNames();
    } catch (error) {
      console.error('Error getting exercises:', error);
      return [];
    }
  }, [currentMuscleGroup, getExercisesByMuscleGroup, getAllUniqueExerciseNames]);
  // Handle muscle group change - cross-selection logic
  const handleMuscleGroupChange = (muscleGroup: string) => {
    form.setValue('muscleGroup', muscleGroup);
    
    // If current exercise doesn't belong to selected muscle group, clear it
    if (currentExercise && muscleGroup) {
      try {
        const exerciseMuscleGroups = getMuscleGroupsByExercise(currentExercise);
        if (!exerciseMuscleGroups.includes(muscleGroup)) {
          form.setValue('exercise', '');
        }
      } catch (error) {
        console.error('Error checking exercise muscle groups:', error);
        // In case of error, clear the exercise to be safe
        form.setValue('exercise', '');
      }
    }
  };

  // Handle exercise change - cross-selection logic
  const handleExerciseChange = (exercise: string) => {
    form.setValue('exercise', exercise);
    
    // If no muscle group selected or current muscle group doesn't match exercise, update it
    if (!currentMuscleGroup && exercise) {
      try {
        const exerciseMuscleGroups = getMuscleGroupsByExercise(exercise);
        if (exerciseMuscleGroups.length > 0) {
          form.setValue('muscleGroup', exerciseMuscleGroups[0]);
        }
      } catch (error) {
        console.error('Error getting muscle groups for exercise:', error);
      }
    } else if (currentMuscleGroup && exercise) {
      try {
        const exerciseMuscleGroups = getMuscleGroupsByExercise(exercise);
        if (!exerciseMuscleGroups.includes(currentMuscleGroup)) {
          if (exerciseMuscleGroups.length > 0) {
            form.setValue('muscleGroup', exerciseMuscleGroups[0]);
          }
        }
      } catch (error) {
        console.error('Error validating muscle group for exercise:', error);
      }
    }
  };

  const onSubmit = async (data: FormData) => {
    if (isSubmitting) return; // Prevent double submission
    
    setIsSubmitting(true);
    try {
      // Create base entry data
      const baseEntry = {
        date: data.date,
        muscleGroup: data.muscleGroup,
        exercise: data.exercise,
        reps: data.reps,
        weight: data.weight,
        rating: data.rating,
        restTime: data.restTime,
      };

      const weight = data.weight;
      const metricWeight = getWeightUnit() === 'lbs' ? weight * 0.453592 : weight;

      if (editEntry && 'id' in editEntry && editEntry.id) {
        // If editEntry is a full WorkoutLogEntry, update it
        const updatedEntry: WorkoutLogEntry = {
          id: editEntry.id,
          ...baseEntry,
          ...(editEntry.rowIndex !== undefined && { rowIndex: editEntry.rowIndex }),
          isSynced: editEntry.isSynced ?? false,
          pendingAction: 'update'
        };
        await editWorkoutLog(updatedEntry);
      } else {
        // For new entries or partial entries, create new
        await addWorkoutLog(baseEntry);
      }
      
      // Success - close modal and reset form
      handleClose();
    } catch (error) {
      console.error('Failed to save workout log:', error);
      // You could add a toast notification here for better UX
      // toast.error('Failed to save workout log. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog 
      open={isOpen} 
      onOpenChange={(open) => {
        if (!open) {
          handleClose();
        }
      }}
    >
      <DialogContent className="sm:max-w-[600px] max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>
            {editEntry && 'id' in editEntry ? 'Edit Workout Log' : 'Add New Workout Log'}
          </DialogTitle>
        </DialogHeader>
        
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <FormField
                control={form.control}
                name="date"
                render={({ field }) => (
                  <FormItem className="col-span-full">
                    <FormLabel>Date</FormLabel>
                    <FormControl>
                      <Input type="date" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
                <FormField
                control={form.control}
                name="muscleGroup"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Muscle Group</FormLabel>                    <FormControl>
                      <AutocompleteInput
                        key={`muscle-group-${modalKey}`}
                        value={field.value}
                        onChange={handleMuscleGroupChange}
                        suggestions={availableMuscleGroups}
                        placeholder="Type or select muscle group..."
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              
              <FormField
                control={form.control}
                name="exercise"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Exercise</FormLabel>                    <FormControl>
                      <AutocompleteInput
                        key={`exercise-${modalKey}`}
                        value={field.value}
                        onChange={handleExerciseChange}
                        suggestions={availableExercises}
                        placeholder="Type or select exercise..."
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
                <FormField
                control={form.control}
                name="reps"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Reps</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        min="1"
                        max="1000"
                        {...field}
                        onChange={e => field.onChange(Number(e.target.value))}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />                <FormField
                control={form.control}
                name="weight"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Weight ({getWeightUnit()})</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        min="0"
                        step="0.5"
                        {...field}
                        placeholder={getWeightUnit() === 'kg' ? '60' : '135'}
                        onChange={e => field.onChange(Number(e.target.value))}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
                  <FormField
                control={form.control}
                name="restTime"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Rest Time (seconds)</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        min="0"
                        max="600"
                        value={field.value ?? ''}
                        onChange={e => field.onChange(e.target.value ? Number(e.target.value) : null)}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
            
            <FormField
              control={form.control}
              name="rating"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rating (1-10)</FormLabel>
                  <div className="flex items-center space-x-4">
                    <FormControl>
                      <Slider
                        min={1}
                        max={10}
                        step={1}
                        value={[field.value]}
                        onValueChange={(values) => field.onChange(values[0])}
                        className="flex-1"
                      />
                    </FormControl>
                    <span className="text-lg font-semibold w-8 text-center">
                      {field.value}
                    </span>
                  </div>
                  <FormMessage />
                </FormItem>
              )}
            />
              <DialogFooter>
              <Button type="button" variant="outline" onClick={handleClose} disabled={isSubmitting}>
                Cancel
              </Button>
              <Button type="submit" disabled={isSubmitting}>
                {isSubmitting ? (
                  <>
                    <svg className="animate-spin -ml-1 mr-2 h-4 w-4" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                      <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                    </svg>
                    {editEntry && 'id' in editEntry ? 'Updating...' : 'Adding...'}
                  </>
                ) : (
                  <>
                    {editEntry && 'id' in editEntry ? 'Update' : 'Add'} Workout
                  </>
                )}
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
};

export { AddWorkoutLogModal };
export default AddWorkoutLogModal;
